
.ct-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 20px;
  margin-bottom: 20px;
  width: 100%;
}
.ct-form-form {
  margin-top: 10px;
}
.contact-phone {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.contact-phone h1 {
  margin-top: 40px;
}
.phone-number {
  color: #BA0C2F;
}
.label-red {
  color: #BA0C2F;
}
.contact-form {
  line-height: 3em;
}
input {
  line-height: 2em;
  width: 80%;
  border: 1.2px solid black;
}
textarea {
  width: 80%;
  border: 1.1px solid black;
}
button {
  background-color: #BA0C2F;
  color: white;
  font-size: 16px;
  border-radius: 4px;
  padding: 8px 18px;
  cursor: pointer;
  margin-top: 10px;
  transition: .3s;
}
button:hover {
  background-color: #fff;
  color: #BA0C2F;
  border-color: #BA0C2F;
}

@media screen and (max-width: 650px) {
  .ct-container{
    flex-direction: column;
    line-height: 1em;
  }
  .contact-form {
    padding-left: 10px;
    padding-right: 10px;
    line-height: 2em;
    display: flex;
    flex-direction: column;
  }
  .ct-form-form {
    align-self: center;
    align-content: center;
    width: 100%;
    margin-left: 16%;
  }
}